import { RectRange } from './RectRange';

class Merges {

  /**
   *  Merges
   * @param {RectRange[]} data 
   */
  constructor({ data = [] }) {
    this._ = [];
    this.setData(data);
  }

  /**
   * 
   * @param {Function} cb 
   */
  forEach(cb) {
    this._.forEach(cb);
  }

  /**
   * 
   * @param {RectRange} cr 
   */
  deleteWithin(cr) {
    this._ = this._.filter(it => !it.within(cr));
  }

  /**
   * 
   * @param {int} ri 
   * @param {int} ci  
   * @returns {*}
   */
  getFirstIncludes(ri, ci) {
    for (let i = 0; i < this._.length; i += 1) {
      const it = this._[i];
      if (it.includes(ri, ci)) {
        return it;
      }
    }
    return null;
  }

  /**
   * ,
   * @param {RectRange} cellRange 
   * @returns {Merges}
   */
  filterIntersects(cellRange) {
    return new Merges(this._.filter(it => it.intersects(cellRange)));
  }

  /**
   * 
   * @param {RectRange} cellRange 
   * @returns {boolean}
   */
  intersects(cellRange) {
    for (let i = 0; i < this._.length; i += 1) {
      const it = this._[i];
      if (it.intersects(cellRange)) {
        // console.log('intersects');
        return true;
      }
    }
    return false;
  }

  deleteIntersects(cellRange) {
    for (let i = 0; i < this._.length; i += 1) {
      const it = this._[i];
      if (it.intersects(cellRange)) {
        this._.splice(i, 1);
      }
    }
  }

  /**
   * 
   * @param {RectRange} cellRange 
   * @returns {*}
   */
  union(cellRange) {
    let cr = cellRange;
    const filter = [];
    for (let i = 0; i < this._.length; i += 1) {
      const item = this._[i];
      if (filter.find(e => e === item)) {
        continue;
      }
      if (item.intersects(cr)) {
        filter.push(item);
        cr = item.union(cr);
        i = -1;
      }
    }
    return cr;
  }

  /**
   * , 
   * @param {RectRange} cr 
   */
  add(cr) {
    this.deleteWithin(cr);
    this._.push(cr);
  }

  /**
   * 
   * @param {RectRange} cellRange 
   * @param {int} rn 
   * @param {int} cn 
   */
  move(cellRange, rn, cn) {
    this._.forEach((it1) => {
      const it = it1;
      if (it.within(cellRange)) {
        it.eri += rn;
        it.sri += rn;
        it.sci += cn;
        it.eci += cn;
      }
    });
  }

  // type: row | column
  shift(type, index, n, cbWithin) {
    this._.forEach((cellRange) => {
      const {
        sri, sci, eri, eci,
      } = cellRange;
      const range = cellRange;
      if (type === 'row') {
        if (sri >= index) {
          range.sri += n;
          range.eri += n;
        } else if (sri < index && index <= eri) {
          range.eri += n;
          cbWithin(sri, sci, n, 0);
        }
      } else if (type === 'column') {
        if (sci >= index) {
          range.sci += n;
          range.eci += n;
        } else if (sci < index && index <= eci) {
          range.eci += n;
          cbWithin(sri, sci, 0, n);
        }
      }
    });
  }

  setData(merges = []) {
    this._ = merges.map(merge => RectRange.valueOf(merge));
    return this;
  }

  getData() {
    return this._.map(merge => merge.toString());
  }
}

export { Merges };

export default Merges;
